<template>
  <div>
        <a-input placeholder="请输入Java进程的Pid，例如:1123" v-model="javaId" />
        <a-button type="primary" icon="play-circle"  style="margin-bottom: 10px" @click="attach">
          agent到进程上
        </a-button>
        <template>
          <a-descriptions title="数据监控" layout="vertical" bordered v-if="this.obj != null">
            <a-descriptions-item label="最大堆内存" >
              {{ this.obj.memoryInfo.heapMax }}MB
            </a-descriptions-item>
            <a-descriptions-item label="已使用堆内存">
              {{ this.obj.memoryInfo.heapUsed }}MB
            </a-descriptions-item>
            <a-descriptions-item label="提交的堆内存" :span="3">
              {{ this.obj.memoryInfo.heapCommitted }}MB
            </a-descriptions-item>
            <a-descriptions-item label="最大非堆内存"  >
              {{ this.obj.memoryInfo.noHeapMax }}MB
            </a-descriptions-item>
            <a-descriptions-item label="已使用非堆内存">
              {{ this.obj.memoryInfo.noHeapUsed }}MB
            </a-descriptions-item>
            <a-descriptions-item label="提交的非堆内存" :span="3">
              {{ this.obj.memoryInfo.noHeapCommitted }}MB
            </a-descriptions-item>

            <a-descriptions-item label="程序启动时间">
              {{ this.obj.systemInfo.startDate }}
            </a-descriptions-item>
            <a-descriptions-item label="程序已运行时间" :span="2">
              {{ this.obj.systemInfo.upTime/1000 }}秒
            </a-descriptions-item>
<!--            <a-descriptions-item label="Status" :span="3">-->
<!--              <a-badge status="processing" text="Running" />-->
<!--            </a-descriptions-item>-->
            <a-descriptions-item label="线程数">
              {{ this.obj.threadInfoList.threadCount }}
            </a-descriptions-item>
            <a-descriptions-item label="线程数峰值">
              {{ this.obj.threadInfoList.peekCount }}
            </a-descriptions-item>
            <a-descriptions-item label="守护线程数">
              {{ this.obj.threadInfoList.threadCount }}
            </a-descriptions-item>
            <a-descriptions-item label="系统参数">
              <a-tag color="purple">
                系统名称:
              </a-tag>
               {{ this.obj.systemInfo.osName }}
              <br />
              <a-tag color="cyan">
                Java版本:
              </a-tag>
              {{ this.obj.systemInfo.javaVersion }}
              <br />
              <a-tag color="orange">
                JavaHome:
              </a-tag>
               {{ this.obj.systemInfo.javaHome }}
              <br />
              <a-tag color="red">
                处理器核心数:
              </a-tag>
               {{ this.obj.systemInfo.coreCount }}
              <br />
            </a-descriptions-item>
            <a-descriptions-item label="GC信息" :span="2">
              <div v-for="(gcItem,index) in obj.gcInfo" v-bind:key="index">
                <div style="text-align:left; ">
                  <a-tag color="pink">
                    垃圾回收器名称:
                  </a-tag>
                  {{gcItem.gcName}}
                  <br />
                  <a-tag color="green">
                    回收次数:
                  </a-tag>
                  {{gcItem.gcCount}}
                  <br />
                  <a-tag color="blue">
                    回收时间:
                  </a-tag>
                  {{gcItem.gcTime}}
                  <br />
                </div>
              </div>
            </a-descriptions-item>
            <a-descriptions-item label="线程信息">
               <div v-for="(gcItem1,index1) in obj.threadInfoList.threads" v-bind:key="index1">
                 <div style="text-align:left; ">
                   <a-tag color="pink">
                     线程名:
                   </a-tag>
                   {{gcItem1.threadName}}
                   <br />
                   <a-tag color="green">
                     线程ID:
                   </a-tag>
                   {{gcItem1.threadId}}
                   <br />
                   <a-tag color="blue">
                     线程状态:
                   </a-tag>
                   {{gcItem1.threadState}}
                   <br />
                 </div>
                </div>
            </a-descriptions-item>

          </a-descriptions>
        </template>

  </div>
</template>

<script>
export default {
name: "Monitor",
  data() {
    return {
      webSock: null,
      obj: null,
      javaId:'',
    };
  },
  created() {
    let ip = sessionStorage.getItem("ip")
    const wsuri = "ws://"+ip+":9675/ws/ws";
    this.webSock = new WebSocket(wsuri);
    this.webSock.onmessage = this.webSocketOnMessage;
  },
  methods:{
    webSocketOnMessage(e){
      console.log(e.data);
      if("连接成功，等待发送指令" === e.data){
        this.$message.info("连接成功")
      }else if("agent已经准备就绪" === e.data){
        this.$message.info("agent准备就绪")
        this.go();
      }else{
        this.obj =  JSON.parse(e.data);
        console.log(this.obj);
      }
    },
    go() {
     setInterval( this.send, 2000);
    },
    send(){
      this.webSock.send("monitor")
    },
    attach(){
      this.webSock.send("pid="+this.javaId);
    },
  }
}
</script>

